.Condition_button {
    width: fit-content;
    display: inline-block;
    text-decoration: none;
    border: 2px solid #7E6C5C;
    background: #ECE5D8;
    color: #976632;
    border-radius: 10px;
    padding: 0.7% 0 0.7% 0;
    cursor: pointer;
    transition: 0.1s;
}

.Condition_button:hover {
    color: #FFFFFF;
    background: #b9a089;
}
.Condition_button:active {
    color: #7E6C5C;
    background: #DCCAA8;
}
.Condition_button:has(:checked) {
    color: #ECE5D8;
    background: #7E6C5C;
    appearance: none;
}
input[type="radio"] {
    display: none;
    cursor: pointer;
}
label {
    cursor: pointer;
    padding: 10px;
}